import React from 'react';
import './Compose.css';

export default function Compose(props) {
  let {disable} = props;
  if (disable) {
    return (
      <div className="compose disable">
        <span className={'nonfriend'}>Cc bn khng phi l bn b!</span>
      </div>
    );
  }
  return (
    <div className="compose">
      <input
        type="text"
        className="compose-input"
        placeholder="Nhp tin nhn"
        onKeyUp={props.onKeyUp}
        ref={props.inputRef}
      />
      {
        props.rightItems
      }
    </div>
  );
}